export default {
  props: {
    /**
   * 
   */
    statistics: Boolean,
    label: String,
    disabled: Boolean,
    /*  */
    checkbox: { type: Boolean, default: false },
    /*  */
    checked: { type: Boolean, default: false },
    /**
    * 
    */
    lazy: Boolean,
    /**
     * 
     */
    action: Boolean
  },
  data() {
    return {
      allChecked: this.checked || (this.data && this.data.checked === true)
    }
  },
  methods: {
    handleClick(...param) {
      this.$emit.apply(this, ['click', ...param])
    }
  }
}