export default {
  props: { draggable: Boolean },
  data() {
    return {
      dragging: false,
      isClick: false,
      position: { x: 0, y: 0 },
      pointStart: { x: 0, y: 0 },
      pointEnd: { x: 0, y: 0 },
      pointMin: { x: 0, y: 0 },
      containerMax: { x: 0, y: 0 },
      pointMax: { x: 0, y: 0 },
      clientStart: { x: 0, y: 0 },
      clientEnd: { x: 0, y: 0 }
    }
  },
  methods: {
    onMouseDown(event) {
      if (this.draggable === false) { return }
      event.preventDefault();
      this.onDragStart(event);
      window.addEventListener('mousemove', this.onDragging);
      window.addEventListener('touchmove', this.onDragging);
      window.addEventListener('mouseup', this.onDragEnd);
      window.addEventListener('touchend', this.onDragEnd);
      window.addEventListener('contextmenu', this.onDragEnd);
    },
    onDragStart(event) {
      this.dragging = true;
      this.isClick = true;
      if (event.type === 'touchstart') {
        event.clientY = event.touches[0].clientY;
        event.clientX = event.touches[0].clientX;
      }
      Object.assign(this.clientStart, { x: event.clientX, y: event.clientY })
      this.handlerDragStart && this.handlerDragStart()
    },
    onDragging(event) {
      if (this.dragging === false) { return }
      this.isClick = false;

      if (event.type === 'touchmove') {
        event.clientY = event.touches[0].clientY;
        event.clientX = event.touches[0].clientX;
      }

      Object.assign(this.clientEnd, { x: event.clientX, y: event.clientY })
      Object.assign(this.pointEnd, {
        x: this.pointStart.x + (this.clientEnd.x - this.clientStart.x),
        y: this.pointStart.y + (this.clientEnd.y - this.clientStart.y)
      })
      this.verifyPointEnd()
      if (this.handlerDragging) {
        this.handlerDragging()
      } else {
        throw new Error('handlerDragging is not function')
      }
    },
    onDragEnd() {
      if (this.dragging === false) { return }
      /*
         *  mouseup  click
         *  preventDefault  mouseup  click  DOM 
         */
      setTimeout(() => {
        this.dragging = false;
        if (this.isClick === false) {
          this.verifyPointEnd()
          Object.assign(this.pointStart, { x: this.pointEnd.x, y: this.pointEnd.y })

          if (this.handlerDragEnd) {
            this.handlerDragEnd()
          } else {
            throw new Error('handlerDragEnd is not function')
          }
        }
      }, 0);
      window.removeEventListener('mousemove', this.onDragging);
      window.removeEventListener('touchmove', this.onDragging);
      window.removeEventListener('mouseup', this.onDragEnd);
      window.removeEventListener('touchend', this.onDragEnd);
      window.removeEventListener('contextmenu', this.onDragEnd);
    },
    verifyPointEnd() {
      const { x, y } = this.pointEnd
      let flag = false
      if (x < this.pointMin.x) {
        this.pointEnd.x = this.pointMin.x
        flag = true
      }
      if (y < this.pointMin.y) {
        this.pointEnd.y = this.pointMin.y
        flag = true
      }
      if (x > this.pointMax.x) {
        this.pointEnd.x = this.pointMax.x
        flag = true
      }
      if (y > this.pointMax.y) {
        this.pointEnd.y = this.pointMax.y
        flag = true
      }

      if (flag && this.handlerBoundary) {
        // 
        this.handlerBoundary()
      }
    }
  }
}